import React, { Fragment } from "react";
import { Button } from "antd";
import {
    ModalContent,
    ModalOpenButton,
    useModalContext,
} from "components/ui/Modal";
import { Form, FormContentBase } from "components/ui/Form/Form";
import { AccessControlTypes, ProtectConfiguration } from "types";

export default function ProtectConfigModal({ refetch }) {
    const { onOk, form } = useModalContext();

    function handleOk() {
        onOk("/protect/config", "create", {} as ProtectConfiguration).then(() => refetch());
    }

    return (
        <Fragment>
            <ModalOpenButton requiredRoles={["Administrator", "Operator"]} requiredAccessControls={AccessControlTypes.Create} allowedWithOneWayGitSync={false}>
                <Button children="Create New Configuration" />
            </ModalOpenButton>
            <ModalContent title="Create new PowerShell Protect Configuration" onClickOk={handleOk} formName="create_pspconfig_form">
                <Form schemaName="protectConfig">
                    <FormContentBase
                        name="create_pspconfig_form"
                        form={form}
                        preserve={false}
                    />
                </Form>
            </ModalContent>
        </Fragment>
    );
}
